<template>
    <div>

        <b-row>
            <b-col lg="4">
                <div @paste="onPaste" class="border bg-light" style='cursor:pointer'>
                    Click here and use Control-V to paste the image.
                </div>
                <br>
                <img v-if="imageDataURL" :src="imageDataURL" alt="Pasted Image" /><br>
                <button :disabled="!imageDataURL" @click="onSave">Save</button>

            </b-col>
            <b-col lg="8">
                
                <div class="d-flex align-content-start flex-wrap">
                    <div class="p-2 bd-highlight " v-for="(x, k) in ds" :key="k">
                        <img :src="x" width="120" height="90" class="border" />
                    </div>
                </div>


            </b-col>
        </b-row>

    </div>
</template>
  
<script>
export default {
    components: {
    },
    data() {
        return {
            imageDataURL: null,
            ds: []
        };
    },
    mounted() {
    },

    methods: {
        onSave() {
            this.ds.push(this.imageDataURL);
        },
        onPaste(event) {
            const clipboardData = event.clipboardData || window.clipboardData;
            const items = clipboardData.items;
            for (let i = 0; i < items.length; i++) {
                if (items[i].type.indexOf("image") !== -1) {
                    const imageFile = items[i].getAsFile();
                    this.processImage(imageFile);
                }
            }
        },
        processImage(imageFile) {
            const reader = new FileReader();
            reader.onload = event => {
                this.imageDataURL = event.target.result;
            };
            reader.readAsDataURL(imageFile);
        }
    }

}
</script>
  